import { __decorate } from "tslib";
import { Component, Vue, Prop } from 'vue-property-decorator';
import banner from '@/components/common-components/bannerSlide';
import wx from 'weixin-js-sdk';
let Home = class Home extends Vue {
    constructor() {
        super(...arguments);
        this.redpackClick = false;
        this.myInfo = {};
        this.bannerUrl = 'http://182.92.123.83:4869/8115a9e1ffaea1d3cba8112c26e1e764';
        this.fields = [
            {
                type: 'input',
                modelKey: 'name',
                label: '姓名',
                props: {
                    placeholder: '请输入'
                },
                rules: {
                    required: true
                },
                messages: {
                    required: '请填写姓名'
                }
            },
            {
                type: 'input',
                modelKey: 'schoolName',
                label: '学校名称',
                props: {
                    placeholder: '请输入'
                },
                rules: {
                    required: false
                }
            },
            {
                type: 'radio-group',
                modelKey: 'sex',
                label: '性别',
                props: {
                    options: ['男', '女']
                },
                rules: {
                    required: true
                }
            },
            {
                type: 'input',
                modelKey: 'phone',
                label: '手机号',
                props: {
                    placeholder: '请输入'
                },
                rules: {
                    required: true
                }
            },
            {
                type: 'input',
                modelKey: 'idcard',
                label: '身份证',
                props: {
                    placeholder: '身份信息我们会严格保密'
                },
                rules: {
                    required: true
                }
            }
        ];
        this.openid = '';
        this.model = {
            name: '',
            schoolName: '',
            phone: '',
            idcard: '',
            radioValue: '男'
        };
        this.isJoin = false;
        this.concatUsUrl = 'https://s1.ax1x.com/2020/07/07/UF5e91.md.png';
        this.inviteUser = '';
    }
    mounted() {
        // this.getWeiXinConfig()
        this.$store.commit('SET_SESSION_ID', 'noSession');
        this.pageInit();
    }
    pageInit() {
        let query = this.$route.query;
        if (query.state) {
            let stepInfoArr = query.state.split('@');
            if (stepInfoArr[0] === 'setupUser') {
                this.inviteUser = stepInfoArr[1];
            }
        }
        if (query.openid) {
            this.openid = query.openid;
            // 用户从排行调过来
        }
        else if (query.code) {
            this.getUserInfo();
        }
    }
    getUserInfo() {
        try {
            let query = this.$route.query;
            if (query.code) {
                let toast = this['$createToast']({
                    time: 0,
                    txt: '数据获取中...',
                    mask: true
                });
                toast.show();
                this.$store.dispatch('getCommodityUserInfo', { code: query.code }).then((response) => {
                    toast.hide();
                    if (response.success) {
                        if (response.data.openid) {
                            this.openid = response.data.openid;
                            if (response.data.partInfo) {
                                this.isJoin = true;
                            }
                            if (response.data.weixinInfo) {
                                this.$set(this, 'myInfo', response.data.weixinInfo);
                            }
                            else {
                                wx.closeWindow();
                            }
                        }
                    }
                });
            }
            else {
                console.log('不是微信登陆');
            }
        }
        catch (error) {
            console.log(error);
        }
    }
    submitHandler(e) {
        e.preventDefault();
        console.log('submit', e);
        console.log(this.model);
        const toast = this['$createToast']({
            time: 0,
            mask: true,
            txt: '数据提交中...'
        });
        toast.show();
        this.$store.dispatch('workSignUp', { ...this.model, inviteUser: this.inviteUser, signupUser: this.openid }).then((response) => {
            toast.hide();
            if (response.success) {
                this.isJoin = true;
            }
            else {
                const toastTip = this['$createToast']({
                    time: 2000,
                    txt: response.message,
                    type: 'txt'
                });
                toastTip.show();
            }
        });
    }
    imgClick(url) {
        wx.previewImage({
            current: url,
            urls: [url] // 需要预览的图片http链接列表
        });
    }
    validateHandler(result) {
        console.log(result);
        console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex);
    }
    getActiveShareCode() {
        const toast = this['$createToast']({
            time: 0,
            txt: '邀请码获取中...',
            mask: true
        });
        toast.show();
        this.$store.dispatch('getActiveShareCode', { openid: this.openid }).then((response) => {
            toast.hide();
            if (response.success) {
                this.redpackClick = false;
                this.$createDialog({
                    type: 'alert',
                    confirmBtn: {
                        text: '我知道了',
                        active: true
                    }
                }, (createElement) => {
                    return [
                        createElement('div', {
                            'class': {
                                'my-title': true
                            },
                            slot: 'title'
                        }, [
                            createElement('p', '邀请同学扫码报名')
                        ]),
                        createElement('img', {
                            'class': {
                                'code-img': true
                            },
                            'attrs': {
                                src: response.data.url
                            },
                            slot: 'content'
                        })
                    ];
                }).show();
            }
        });
    }
    toUs() {
        window.open('https://work.weixin.qq.com/kfid/kfcf5652ca9b8d74f0b', '_top');
    }
};
__decorate([
    Prop({ default: 'default value' })
], Home.prototype, "propB", void 0);
Home = __decorate([
    Component({
        components: {
            banner
        },
        filters: {
            getFormatDate(date) {
                return new Date(date).getFullYear() + '年' + (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '号';
            },
            getPhone(item) {
                if (item && item.user_info) {
                    return item.user_info[0].phone;
                }
                else {
                    return '';
                }
            },
            getAddress(item) {
                if (item && item.user_info) {
                    return item.user_info[0].address;
                }
                else {
                    return '';
                }
            }
        }
    })
], Home);
export default Home;
